/* global document, Office, Excel */

// Voice recognition variables
let recognition = null;
let isListening = false;

// Backend API URL - Vercel    URL 
//  URL   (CORS     URL  )
const API_PROXY_URL = process.env.API_PROXY_URL || 'https://excel-addon-backend.vercel.app/api/openai-proxy-debug';

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    // Test backend connection
    testBackendConnection();
    
    // Assign event handlers
    document.getElementById("executeButton").onclick = executeCommand;
    document.getElementById("clearButton").onclick = clearInput;
    document.getElementById("voiceButton").onclick = toggleVoiceRecognition;
    document.getElementById("settingsButton").onclick = showSettings;

    // Handle example clicks
    const exampleItems = document.querySelectorAll('.example-item');
    exampleItems.forEach(item => {
      item.onclick = function() {
        setCommand(this.getAttribute('data-command'));
      };
    });

    // Handle Enter key
    document.getElementById('commandInput').addEventListener('keydown', function(event) {
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault();
        executeCommand();
      }
    });

    // Initialize speech recognition
    initializeSpeechRecognition();
  }
});

// Initialize speech recognition
function initializeSpeechRecognition() {
  const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
  
  if (!SpeechRecognition) {
    showStatus('    .', 'error');
    document.getElementById('voiceButton').disabled = true;
    document.getElementById('voiceStatus').textContent = '  ';
    return null;
  }
  
  recognition = new SpeechRecognition();
  recognition.lang = 'ko-KR';
  recognition.continuous = false;
  recognition.interimResults = true;
  recognition.maxAlternatives = 1;
  
  recognition.onstart = function() {
    isListening = true;
    document.getElementById('voiceButton').classList.add('listening');
    document.getElementById('voiceStatus').textContent = ' ... ';
    document.getElementById('voiceStatus').classList.add('listening');
  };
  
  recognition.onresult = function(event) {
    const transcript = event.results[0][0].transcript;
    document.getElementById('commandInput').value = transcript;
    
    if (event.results[0].isFinal) {
      document.getElementById('voiceStatus').textContent = '  ';
    } else {
      document.getElementById('voiceStatus').textContent = ': ' + transcript;
    }
  };
  
  recognition.onerror = function(event) {
    isListening = false;
    document.getElementById('voiceButton').classList.remove('listening');
    document.getElementById('voiceStatus').classList.remove('listening');
    
    let errorMessage = '  ';
    switch(event.error) {
      case 'no-speech':
        errorMessage = '  .';
        break;
      case 'audio-capture':
        errorMessage = '   .';
        break;
      case 'not-allowed':
        errorMessage = '  .';
        break;
      case 'network':
        errorMessage = '  .';
        break;
    }
    
    document.getElementById('voiceStatus').textContent = errorMessage;
    showStatus(errorMessage, 'error');
  };
  
  recognition.onend = function() {
    isListening = false;
    document.getElementById('voiceButton').classList.remove('listening');
    document.getElementById('voiceStatus').classList.remove('listening');
    
    const command = document.getElementById('commandInput').value.trim();
    if (command) {
      document.getElementById('voiceStatus').textContent = '  .   .';
      document.getElementById('executeButton').focus();
    } else {
      document.getElementById('voiceStatus').textContent = '  ';
    }
  };
  
  return recognition;
}

// Toggle voice recognition
function toggleVoiceRecognition() {
  if (!recognition) {
    recognition = initializeSpeechRecognition();
    if (!recognition) return;
  }
  
  if (isListening) {
    recognition.stop();
  } else {
    recognition.start();
  }
}

// Execute command
// Add flag to prevent duplicate execution
let isExecuting = false;

async function executeCommand() {
  // Prevent duplicate execution
  if (isExecuting) {
    console.log('Command already executing, ignoring duplicate call');
    return;
  }
  
  const command = document.getElementById('commandInput').value.trim();
  
  if (!command) {
    showStatus(' .', 'error');
    return;
  }
  
  isExecuting = true;
  
  const button = document.getElementById('executeButton');
  const originalText = '<span class="ms-Button-label"></span>';
  
  // Show loading state
  button.disabled = true;
  button.innerHTML = '<span class="loading"></span><span>...</span>';
  showStatus('  ...', 'info');
  
  try {
    await Excel.run(async (context) => {
      console.log('Starting Excel.run for command execution');
      // Get current worksheet context
      const worksheet = context.workbook.worksheets.getActiveWorksheet();
      const range = context.workbook.getSelectedRange();
      
      // Load necessary properties
      worksheet.load("name");
      range.load(["address", "rowIndex", "columnIndex", "rowCount", "columnCount"]);
      
      // Get sheet data for context
      const usedRange = worksheet.getUsedRange();
      usedRange.load(["rowCount", "columnCount", "values"]);
      
      await context.sync();
      
      // Build sheet context
      const sheetContext = {
        sheetName: worksheet.name,
        activeRange: {
          address: range.address,
          row: range.rowIndex + 1,
          column: range.columnIndex + 1,
          numRows: range.rowCount,
          numColumns: range.columnCount
        },
        lastRow: usedRange ? usedRange.rowCount : 0,
        lastColumn: usedRange ? usedRange.columnCount : 0,
        headers: [],
        dataRange: usedRange ? usedRange.values : [],
        isLargeSheet: usedRange && usedRange.rowCount > 1000
      };
      
      // Extract headers
      if (usedRange && usedRange.rowCount > 0) {
        const headerRow = usedRange.values[0];
        for (let i = 0; i < headerRow.length; i++) {
          sheetContext.headers.push({
            column: i + 1,
            columnLetter: getColumnLetter(i),
            label: headerRow[i] ? headerRow[i].toString() : ''
          });
        }
      }
      
      // Call OpenAI API to interpret the command
      const interpretation = await callOpenAI(command, sheetContext);
      
      if (!interpretation.success) {
        throw new Error(interpretation.error);
      }
      
      // Execute the interpreted command
      console.log('Executing interpreted command...');
      const result = await executeInterpretedCommand(context, interpretation.data);
      console.log('Command execution result:', result);
      
      // Reset button and show success
      button.disabled = false;
      button.innerHTML = originalText;
      
      if (result.success) {
        console.log('Operation completed successfully:', result);
        showStatus(result.message || '  .', 'success');
        setTimeout(() => {
          document.getElementById('commandInput').value = '';
        }, 1000);
      } else {
        console.error('Operation failed:', result);
        showStatus(result.error || '  .', 'error');
      }
      
      console.log('Excel.run completing...');
    });
    console.log('Excel.run completed');
  } catch (error) {
    console.error('Error in executeCommand:', error);
    console.error('Error stack:', error.stack);
    button.disabled = false;
    button.innerHTML = originalText;
    showStatus(' : ' + error.message, 'error');
  } finally {
    // Reset execution flag
    isExecuting = false;
  }
}

// Call OpenAI API through proxy
async function callOpenAI(command, sheetContext) {
  try {
    const response = await fetch(API_PROXY_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        command: command,
        sheetContext: sheetContext
      })
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      return {
        success: false,
        error: errorData.error || `  (${response.status})`
      };
    }
    
    return await response.json();
    
  } catch (error) {
    console.error('Proxy API Error:', error);
    return {
      success: false,
      error: `API  : ${error.toString()}`
    };
  }
}

// Original OpenAI API call (no longer used)
async function callOpenAIDirectly(command, sheetContext) {
  const systemPrompt = `You are an Excel assistant that interprets natural language commands and returns JSON instructions for Excel operations.
  
Available operations:
1. merge: Merge cells
2. sum: Sum values in a range or column
3. average: Calculate average
4. count: Count cells (can count all, numbers only, or based on conditions)
5. format: Format cells (bold, italic, font color, background color, etc.)
6. sort: Sort data
7. filter: Filter data
8. insert: Insert rows/columns
9. delete: Delete rows/columns
10. formula: Add custom formula
11. chart: Create chart
12. conditional_format: Add conditional formatting
13. translate: Translate cell contents to another language
14. compress: Remove empty rows in a specific column range
15. retry_translation: Retry translation for failed items marked as [ ]

For sum operation:
- If user mentions a column by header name (e.g., "totalToken  ", "totalToken "), return: { "sumType": "column", "columnName": "totalToken" }
- The system will automatically find the column, determine the data range, and place the sum in the first empty cell below the data
- For specific range sum, use: { "sourceRange": "A2:A10" }
- For adding sum below selection, use: { "addNewRow": true }

Current sheet context:
- Active range: ${sheetContext.activeRange.address}
- Sheet dimensions: ${sheetContext.lastRow} rows x ${sheetContext.lastColumn} columns
- Headers: ${sheetContext.headers.map(h => `Column ${h.columnLetter}: "${h.label}"`).join(', ')}

Return JSON in this format:
{
  "operation": "operation_name",
  "parameters": {
    // operation-specific parameters
  }
}`;

  const url = 'https://api.openai.com/v1/chat/completions';
  
  const payload = {
    model: 'gpt-4.1-2025-04-14',
    messages: [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: `Current Excel state:
Headers: ${sheetContext.headers.map(h => `Column ${h.columnLetter}: "${h.label}"`).join(', ')}
Active sheet: ${sheetContext.sheetName}

User command: ${command}` }
    ],
    temperature: 0.3,
    max_tokens: 500
  };
  
  try {
    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${OPENAI_API_KEY}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(payload)
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      if (response.status === 429) {
        return {
          success: false,
          error: 'API   .    .'
        };
      } else if (response.status === 401) {
        return {
          success: false,
          error: 'API   . API  .'
        };
      }
      return {
        success: false,
        error: `API  (${response.status}): ${errorData.error?.message || '   '}`
      };
    }
    
    const result = await response.json();
    
    if (result.choices && result.choices[0]) {
      const content = result.choices[0].message.content;
      try {
        const parsedCommand = JSON.parse(content);
        return {
          success: true,
          data: parsedCommand
        };
      } catch (parseError) {
        console.error('Failed to parse AI response:', content);
        return {
          success: false,
          error: 'AI    .  .'
        };
      }
    } else {
      return {
        success: false,
        error: 'OpenAI API    .'
      };
    }
  } catch (error) {
    console.error('OpenAI API Error:', error);
    return {
      success: false,
      error: `OpenAI API : ${error.toString()}`
    };
  }
}

// Execute the interpreted command
async function executeInterpretedCommand(context, commandData) {
  const operation = commandData.operation;
  const params = commandData.parameters || {};
  
  console.log(`[${new Date().toISOString()}] Executing operation: ${operation} with params:`, params);
  
  try {
    switch (operation) {
      case 'merge':
        return await executeMerge(context, params);
      
      case 'sum':
        return await executeSum(context, params);
      
      case 'average':
        return await executeAverage(context, params);
      
      case 'count':
        return await executeCount(context, params);
      
      case 'format':
        return await executeFormat(context, params);
      
      case 'sort':
        return await executeSort(context, params);
      
      case 'filter':
        return await executeFilter(context, params);
      
      case 'insert':
        return await executeInsert(context, params);
      
      case 'delete':
        return await executeDelete(context, params);
      
      case 'formula':
        return await executeFormula(context, params);
      
      case 'chart':
        return await executeChart(context, params);
      
      case 'conditional_format':
        return await executeConditionalFormat(context, params);
      
      case 'translate':
        return await executeTranslate(context, params);
      
      case 'compress':
        return await executeCompress(context, params);
      
      case 'retry_translation':
        return await executeRetryTranslation(context, params);
      
      default:
        return {
          success: false,
          error: `   : ${operation}`
        };
    }
  } catch (error) {
    console.error('Error in executeInterpretedCommand:', error);
    return {
      success: false,
      error: `   : ${error.message || error.toString()}`
    };
  }
}

// Merge cells
async function executeMerge(context, params) {
  console.log('executeMerge started with params:', params);
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : context.workbook.getSelectedRange();
  
  console.log('Loading range address...');
  // Load address property before using it
  range.load('address');
  await context.sync();
  
  console.log('Range address loaded:', range.address);
  const rangeAddress = range.address;
  
  console.log('Merging range...');
  range.merge();
  await context.sync();
  
  console.log('Merge completed successfully');
  return {
    success: true,
    message: `${rangeAddress}  .`
  };
}

// Sum values
async function executeSum(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  
  // If column name is provided, find the column and create range
  if (params.columnName || params.sumType === 'column') {
    console.log('Sum by column name:', params.columnName);
    
    // Get sheet context to find column by name
    const usedRange = worksheet.getUsedRange();
    usedRange.load(["values", "rowCount", "columnCount"]);
    await context.sync();
    
    if (!usedRange || usedRange.rowCount === 0) {
      return {
        success: false,
        error: '  .'
      };
    }
    
    // Find column by header name
    const headers = usedRange.values[0];
    let columnIndex = -1;
    let columnLetter = '';
    
    for (let i = 0; i < headers.length; i++) {
      if (headers[i] && headers[i].toString().toLowerCase() === (params.columnName || '').toLowerCase()) {
        columnIndex = i;
        columnLetter = getColumnLetter(i);
        break;
      }
    }
    
    if (columnIndex === -1) {
      return {
        success: false,
        error: `"${params.columnName}"    .`
      };
    }
    
    // Find last row with data in this column
    let lastDataRow = 1; // Start from row 2 (after header)
    for (let row = 1; row < usedRange.rowCount; row++) {
      if (usedRange.values[row][columnIndex] !== null && 
          usedRange.values[row][columnIndex] !== undefined && 
          usedRange.values[row][columnIndex] !== '') {
        lastDataRow = row + 1; // +1 because Excel rows are 1-indexed
      }
    }
    
    // Create range from row 2 to last data row
    const rangeAddress = `${columnLetter}2:${columnLetter}${lastDataRow}`;
    const sumCell = worksheet.getCell(lastDataRow, columnIndex); // getCell uses 0-based row index
    
    console.log(`Summing range: ${rangeAddress}, placing result in row ${lastDataRow + 1}`);
    
    sumCell.formulas = [[`=SUM(${rangeAddress})`]];
    await context.sync();
    
    sumCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${params.columnName}   ${sumCell.address} : ${formatNumber(sumCell.values[0][0])}`,
      value: sumCell.values[0][0]
    };
  }
  
  // Original logic for range-based sum
  const sourceRange = params.sourceRange ? worksheet.getRange(params.sourceRange) : context.workbook.getSelectedRange();
  
  sourceRange.load(["columnIndex", "rowIndex", "rowCount", "address"]);
  await context.sync();
  
  if (params.addNewRow === true) {
    // Add new row for sum
    const column = sourceRange.columnIndex;
    const lastRow = sourceRange.rowIndex + sourceRange.rowCount;
    const newCell = worksheet.getCell(lastRow, column);
    
    newCell.formulas = [[`=SUM(${sourceRange.address})`]];
    await context.sync();
    
    newCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${newCell.address}  : ${formatNumber(newCell.values[0][0])}`,
      value: newCell.values[0][0]
    };
  } else if (params.targetCell) {
    // Sum to specific cell
    const targetCell = worksheet.getRange(params.targetCell);
    targetCell.formulas = [[`=SUM(${sourceRange.address})`]];
    await context.sync();
    
    targetCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${targetCell.address}  : ${formatNumber(targetCell.values[0][0])}`,
      value: targetCell.values[0][0]
    };
  } else {
    // Default: add sum below the range
    const column = sourceRange.columnIndex;
    const lastRow = sourceRange.rowIndex + sourceRange.rowCount;
    const newCell = worksheet.getCell(lastRow, column);
    
    newCell.formulas = [[`=SUM(${sourceRange.address})`]];
    await context.sync();
    
    newCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${newCell.address}  : ${formatNumber(newCell.values[0][0])}`,
      value: newCell.values[0][0]
    };
  }
}

// Calculate average
async function executeAverage(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const sourceRange = params.sourceRange ? worksheet.getRange(params.sourceRange) : context.workbook.getSelectedRange();
  
  sourceRange.load(["columnIndex", "rowIndex", "rowCount", "address"]);
  await context.sync();
  
  if (params.targetCell) {
    const targetCell = worksheet.getRange(params.targetCell);
    targetCell.formulas = [[`=AVERAGE(${sourceRange.address})`]];
    await context.sync();
    
    targetCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${targetCell.address}  : ${formatNumber(targetCell.values[0][0])}`,
      value: targetCell.values[0][0]
    };
  } else {
    const column = sourceRange.columnIndex;
    const lastRow = sourceRange.rowIndex + sourceRange.rowCount;
    const newCell = worksheet.getCell(lastRow, column);
    
    newCell.formulas = [[`=AVERAGE(${sourceRange.address})`]];
    await context.sync();
    
    newCell.load(["values", "address"]);
    await context.sync();
    
    return {
      success: true,
      message: `${newCell.address}  : ${formatNumber(newCell.values[0][0])}`,
      value: newCell.values[0][0]
    };
  }
}

// Count cells
async function executeCount(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const sourceRange = params.sourceRange ? worksheet.getRange(params.sourceRange) : context.workbook.getSelectedRange();
  
  sourceRange.load(["address"]);
  await context.sync();
  
  let formula;
  if (params.countType === 'countif' && params.condition !== undefined) {
    let criteria;
    if (params.operator === 'contains') {
      criteria = `"*${params.condition}*"`;
    } else if (params.operator === 'equals') {
      criteria = typeof params.condition === 'string' ? `"${params.condition}"` : params.condition;
    } else if (params.operator && ['>', '<', '>=', '<=', '<>'].includes(params.operator)) {
      criteria = `"${params.operator}${params.condition}"`;
    } else {
      if (typeof params.condition === 'string') {
        criteria = `"*${params.condition}*"`;
      } else {
        criteria = params.condition;
      }
    }
    formula = `=COUNTIF(${sourceRange.address}, ${criteria})`;
  } else if (params.countType === 'counta') {
    formula = `=COUNTA(${sourceRange.address})`;
  } else {
    formula = `=COUNT(${sourceRange.address})`;
  }
  
  let resultCell;
  if (params.targetCell) {
    resultCell = worksheet.getRange(params.targetCell);
  } else {
    const usedRange = worksheet.getUsedRange();
    await context.sync();
    
    const lastRow = usedRange ? usedRange.rowCount : 1;
    const lastCol = usedRange ? usedRange.columnCount : 1;
    resultCell = worksheet.getCell(lastRow, lastCol);
  }
  
  resultCell.formulas = [[formula]];
  await context.sync();
  
  resultCell.load("values");
  await context.sync();
  
  return {
    success: true,
    message: params.countType === 'countif' ? 
      `"${params.condition}"()   : ${formatNumber(resultCell.values[0][0])}` :
      ` : ${formatNumber(resultCell.values[0][0])}`,
    value: resultCell.values[0][0]
  };
}

// Format cells
async function executeFormat(context, params) {
  console.log('executeFormat called with params:', params);
  
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : context.workbook.getSelectedRange();
  
  range.load("format");
  await context.sync();
  
  if (params.bold !== undefined) {
    range.format.font.bold = params.bold;
  }
  
  if (params.italic !== undefined) {
    range.format.font.italic = params.italic;
  }
  
  if (params.fontSize) {
    range.format.font.size = params.fontSize;
  }
  
  if (params.fontColor) {
    range.format.font.color = params.fontColor;
  }
  
  if (params.backgroundColor) {
    range.format.fill.color = params.backgroundColor;
  }
  
  if (params.horizontalAlignment) {
    range.format.horizontalAlignment = params.horizontalAlignment === 'left' ? 'Left' :
                                     params.horizontalAlignment === 'center' ? 'Center' :
                                     params.horizontalAlignment === 'right' ? 'Right' : 'General';
  }
  
  if (params.numberFormat) {
    // Handle specific format types
    let format = params.numberFormat;
    if (format === 'number' || format === '') {
      format = '#,##0';
    } else if (format === 'currency' || format === '' || format === 'won' || format === '') {
      format = '#,##0';
    } else if (format === 'currency_decimal' || format === '_') {
      format = '#,##0.00';
    } else if (format === 'percentage' || format === '' || format === '') {
      format = '0%';
    } else if (format === 'percentage_decimal' || format === '_') {
      format = '0.00%';
    } else if (format === 'date' || format === '') {
      format = 'yyyy-mm-dd';
    } else if (format === 'time' || format === '') {
      format = 'hh:mm:ss';
    } else if (format === 'text' || format === '') {
      format = '@';
    } else if (format === 'general' || format === '') {
      format = 'General';
    }
    
    // Set number format for the range
    range.numberFormat = format;
  }
  
  await context.sync();
  
  return {
    success: true,
    message: ' .'
  };
}

// Sort data
async function executeSort(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : worksheet.getUsedRange();
  
  const column = params.column || 1;
  const ascending = params.ascending !== undefined ? params.ascending : true;
  
  range.sort.apply([
    {
      key: column - 1, // Excel API uses 0-based index
      ascending: ascending
    }
  ]);
  
  await context.sync();
  
  const columnLetter = getColumnLetter(column - 1);
  
  return {
    success: true,
    message: `${columnLetter}  ${ascending ? '' : ''} .`
  };
}

// Create chart
async function executeChart(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const sourceData = params.range ? worksheet.getRange(params.range) : context.workbook.getSelectedRange();
  
  console.log('Creating chart with params:', params);
  
  // Load the source data to ensure it's available
  sourceData.load(["address", "values"]);
  await context.sync();
  
  console.log('Chart data range:', sourceData.address);
  
  // Determine chart type
  let chartType;
  if (params.chartType === 'bar' || params.chartType === '') {
    chartType = Excel.ChartType.columnClustered;
  } else if (params.chartType === 'line' || params.chartType === '') {
    chartType = Excel.ChartType.line;
  } else if (params.chartType === 'pie' || params.chartType === '') {
    chartType = Excel.ChartType.pie;
  } else if (params.chartType === 'scatter' || params.chartType === '') {
    chartType = Excel.ChartType.xyscatter;
  } else {
    // Default to column chart
    chartType = Excel.ChartType.columnClustered;
  }
  
  // Create the chart
  const chart = worksheet.charts.add(chartType, sourceData, Excel.ChartSeriesBy.auto);
  
  // Set chart properties
  chart.title.text = params.title || '';
  chart.height = 300;
  chart.width = 400;
  
  // Position the chart
  chart.left = params.offsetX || 100;
  chart.top = params.offsetY || 100;
  
  // Set legend position
  chart.legend.position = Excel.ChartLegendPosition.bottom;
  chart.legend.visible = true;
  
  await context.sync();
  
  return {
    success: true,
    message: `${sourceData.address}  ${params.chartType || ''}  .`
  };
}

// Add conditional formatting
async function executeConditionalFormat(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : worksheet.getUsedRange();
  
  console.log('Applying conditional format with params:', params);
  
  // Simply apply conditional format to the entire range
  // Excel will automatically skip non-numeric cells for numeric comparisons
  const conditionalFormat = range.conditionalFormats.add(Excel.ConditionalFormatType.cellValue);
  
  // Set the rule based on condition
  if (params.condition === 'greater_than' && params.value !== undefined) {
    conditionalFormat.cellValue.rule = {
      formula1: params.value.toString(),
      operator: Excel.ConditionalCellValueOperator.greaterThan
    };
  } else if (params.condition === 'less_than' && params.value !== undefined) {
    conditionalFormat.cellValue.rule = {
      formula1: params.value.toString(),
      operator: Excel.ConditionalCellValueOperator.lessThan
    };
  } else if (params.condition === 'equal_to' && params.value !== undefined) {
    conditionalFormat.cellValue.rule = {
      formula1: params.value.toString(),
      operator: Excel.ConditionalCellValueOperator.equalTo
    };
  } else if (params.condition === 'text_contains' && params.value !== undefined) {
    conditionalFormat.cellValue.rule = {
      formula1: params.value.toString(),
      operator: Excel.ConditionalCellValueOperator.containsText
    };
  } else {
    // Default to greater than
    conditionalFormat.cellValue.rule = {
      formula1: (params.value || 0).toString(),
      operator: Excel.ConditionalCellValueOperator.greaterThan
    };
  }
  
  // Set the format
  conditionalFormat.cellValue.format.fill.color = params.backgroundColor || "#00FF00";
  if (params.fontColor) {
    conditionalFormat.cellValue.format.font.color = params.fontColor;
  }
  if (params.bold) {
    conditionalFormat.cellValue.format.font.bold = true;
  }
  
  await context.sync();
  
  return {
    success: true,
    message: `  . (${params.condition} ${params.value || ''})`
  };
}

// Translate column contents
async function executeTranslate(context, params) {
  console.log('executeTranslate called with params:', params);
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  
  if (!params.targetLanguage) {
    return { success: false, error: '  .' };
  }
  
  let sourceRange;
  let targetColumnIndex;
  
  if (params.sourceRange) {
    // Handle column ranges like "C:C" by intersecting with used range
    if (params.sourceRange.match(/^[A-Z]+:[A-Z]+$/)) {
      console.log('Handling column range:', params.sourceRange);
      const usedRange = worksheet.getUsedRange();
      if (!usedRange) {
        return { success: false, error: '  .' };
      }
      
      usedRange.load(["rowCount", "columnCount"]);
      await context.sync();
      
      console.log('Used range:', {
        rowCount: usedRange.rowCount,
        columnCount: usedRange.columnCount
      });
      
      // Get the column letter from the range (e.g., "C" from "C:C")
      const columnLetter = params.sourceRange.split(':')[0];
      const columnIndex = columnLetter.charCodeAt(0) - 65;
      
      // Get only the used portion of the column, starting from row 2 (skip header)
      if (usedRange.rowCount > 1) {
        sourceRange = worksheet.getRangeByIndexes(1, columnIndex, usedRange.rowCount - 1, 1);
      } else {
        return { success: false, error: '  .' };
      }
      
      // Handle target range similarly
      if (params.targetRange && params.targetRange.match(/^[A-Z]+:[A-Z]+$/)) {
        const targetColumnLetter = params.targetRange.split(':')[0];
        targetColumnIndex = targetColumnLetter.charCodeAt(0) - 65;
        console.log('Target column calculation:', {
          targetRange: params.targetRange,
          targetColumnLetter,
          targetColumnIndex
        });
      } else {
        // If no target specified, use next column
        targetColumnIndex = columnIndex + 1;
        console.log('Using next column as target:', {
          sourceColumnIndex: columnIndex,
          targetColumnIndex
        });
      }
    } else {
      sourceRange = worksheet.getRange(params.sourceRange);
    }
  } else if (params.sourceColumn) {
    const columnIndex = params.sourceColumn.charCodeAt(0) - 65;
    const usedRange = worksheet.getUsedRange();
    usedRange.load(["rowCount"]);
    await context.sync();
    
    if (usedRange.rowCount > 1) {
      sourceRange = worksheet.getRangeByIndexes(1, columnIndex, usedRange.rowCount - 1, 1);
    } else {
      return { success: false, error: '  .' };
    }
    
    targetColumnIndex = params.targetColumn === 'next' ? columnIndex + 1 :
                       params.targetColumn ? params.targetColumn.charCodeAt(0) - 65 : columnIndex + 1;
  } else {
    return { success: false, error: '  .' };
  }
  
  sourceRange.load(["values", "rowIndex", "columnIndex", "rowCount"]);
  await context.sync();
  
  console.log('Source range loaded:', {
    rowIndex: sourceRange.rowIndex,
    columnIndex: sourceRange.columnIndex,
    rowCount: sourceRange.rowCount,
    values: sourceRange.values ? `${sourceRange.values.length} rows` : 'null'
  });
  
  const sourceValues = sourceRange.values;
  if (!sourceValues || sourceValues.length === 0) {
    return { success: false, error: '  .' };
  }
  
  const translations = [];
  const batchSize = 20;
  
  // Translate in batches
  for (let i = 0; i < sourceValues.length; i += batchSize) {
    const batch = sourceValues.slice(i, Math.min(i + batchSize, sourceValues.length));
    const batchTexts = batch.map(row => row[0]).filter(text => text);
    
    if (batchTexts.length > 0) {
      const translatedBatch = await translateBatch(batchTexts, params.targetLanguage, params.sourceLanguage);
      
      // Check if translatedBatch is valid
      if (!translatedBatch || !Array.isArray(translatedBatch)) {
        console.error('Invalid translation batch received:', translatedBatch);
        // Fill with empty strings if translation failed
        for (let j = 0; j < batch.length; j++) {
          translations.push(['']);
        }
      } else {
        console.log('Processing translation batch:', {
          batchLength: batch.length,
          translatedBatchLength: translatedBatch.length,
          sampleTranslations: translatedBatch.slice(0, 3),
          firstTranslation: translatedBatch[0],
          translationType: typeof translatedBatch[0],
          rawData: JSON.stringify(translatedBatch.slice(0, 3))
        });
        
        let translationIndex = 0;
        for (let j = 0; j < batch.length; j++) {
          if (batch[j][0]) {
            const translatedText = translatedBatch[translationIndex] || '';
            translations.push([translatedText]);
            if (j < 3) {
              console.log(`Translation ${j}: "${batch[j][0]}" -> "${translatedText}"`);
            }
            translationIndex++;
          } else {
            translations.push(['']);
          }
        }
      }
    } else {
      for (let j = 0; j < batch.length; j++) {
        translations.push(['']);
      }
    }
    
    // Show progress
    if (i % 100 === 0 && i > 0) {
      showStatus(` ... ${Math.round((i / sourceValues.length) * 100)}%`, 'info');
    }
  }
  
  // Write translations
  console.log('Writing translations to target column:', {
    rowIndex: sourceRange.rowIndex,
    targetColumnIndex: targetColumnIndex || sourceRange.columnIndex + 1,
    translationsCount: translations.length,
    sampleTranslations: translations.slice(0, 3).map(t => t[0])
  });
  console.log('First 5 translations raw:', JSON.stringify(translations.slice(0, 5)));
  console.log('Translation content check:', {
    first: translations[0] ? translations[0][0] : 'null',
    second: translations[1] ? translations[1][0] : 'null',
    third: translations[2] ? translations[2][0] : 'null',
    isEmpty: translations[0] && translations[0][0] === ''
  });
  
  const targetRange = worksheet.getRangeByIndexes(
    sourceRange.rowIndex,
    targetColumnIndex || sourceRange.columnIndex + 1,
    translations.length,
    1
  );
  
  targetRange.load(["address", "values"]);
  await context.sync();
  console.log('Target range address:', targetRange.address);
  console.log('Existing target values (first 3):', targetRange.values.slice(0, 3));
  
  // Clear existing values first
  targetRange.clear(Excel.ClearApplyTo.contents);
  await context.sync();
  console.log('Target range cleared');
  
  // Ensure translations are properly formatted as 2D array
  console.log('Setting target range values:', {
    translationsLength: translations.length,
    firstTranslation: translations[0],
    isArray: Array.isArray(translations),
    is2DArray: Array.isArray(translations[0])
  });
  
  // Try setting values with explicit string conversion
  const stringTranslations = translations.map(row => [String(row[0] || '')]);
  console.log('String translations (first 3):', stringTranslations.slice(0, 3));
  
  // Try using numberFormat to ensure cells are treated as text
  targetRange.numberFormat = [["@"]]; // @ means text format
  await context.sync();
  
  targetRange.values = stringTranslations;
  
  // Force Excel to update
  targetRange.format.autofitColumns();
  targetRange.format.font.color = "#000000"; // Ensure text is visible
  targetRange.format.fill.color = "#FFFFFF"; // White background
  
  await context.sync();
  console.log('Translations written to Excel');
  
  // Verify the values were actually written
  targetRange.load(["values", "text", "valueTypes"]);
  await context.sync();
  console.log('Verification - Target range:', {
    address: targetRange.address,
    values: targetRange.values.slice(0, 3),
    text: targetRange.text.slice(0, 3),
    valueTypes: targetRange.valueTypes.slice(0, 3),
    actualFirstValue: targetRange.values[0] ? targetRange.values[0][0] : 'null',
    firstThreeValues: [
      targetRange.values[0] ? targetRange.values[0][0] : 'empty',
      targetRange.values[1] ? targetRange.values[1][0] : 'empty',
      targetRange.values[2] ? targetRange.values[2][0] : 'empty'
    ]
  });
  
  // Try alternative method - set each cell individually for debugging
  if (translations.length > 0) {
    const isEmpty = !targetRange.values[0] || !targetRange.values[0][0] || targetRange.values[0][0] === '';
    console.log('Checking if values are empty:', {
      isEmpty,
      firstValue: targetRange.values[0] ? targetRange.values[0][0] : 'null',
      firstValueLength: targetRange.values[0] && targetRange.values[0][0] ? targetRange.values[0][0].length : 0
    });
    
    if (isEmpty) {
      console.log('Values not visible, trying individual cell approach...');
      // Try setting just the first few cells individually
      for (let i = 0; i < Math.min(3, translations.length); i++) {
        const cellRow = sourceRange.rowIndex + i;
        const cellCol = targetColumnIndex || sourceRange.columnIndex + 1;
        const cell = worksheet.getCell(cellRow, cellCol);
        const cellValue = stringTranslations[i][0];
        
        console.log(`Setting cell (${cellRow},${cellCol}) to: "${cellValue}"`);
        cell.values = [[cellValue]];
        cell.format.font.color = "#000000";
        cell.format.fill.color = "#FFFF00"; // Yellow background to make it visible
        
        await context.sync();
        
        // Verify it was set
        cell.load("values");
        await context.sync();
        console.log(`Cell ${i} after setting:`, cell.values[0][0]);
      }
    }
  }
  
  // Add header
  const headerCell = worksheet.getCell(0, targetColumnIndex || sourceRange.columnIndex + 1);
  const sourceHeaderCell = worksheet.getCell(0, sourceRange.columnIndex);
  sourceHeaderCell.load("values");
  await context.sync();
  
  headerCell.values = [[`${sourceHeaderCell.values[0][0]} (${params.targetLanguage})`]];
  await context.sync();
  
  return {
    success: true,
    message: ` . (${sourceValues.length} )`
  };
}

// Translate a batch of texts through proxy
async function translateBatch(texts, targetLanguage, sourceLanguage) {
  try {
    console.log('translateBatch called with', texts.length, 'texts');
    const response = await fetch(API_PROXY_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        command: `Translate these items to ${targetLanguage}: ${texts.join(', ')}`,
        sheetContext: {
          operation: 'translate_batch',
          texts: texts,
          targetLanguage: targetLanguage,
          sourceLanguage: sourceLanguage
        }
      })
    });
    
    console.log('Translation response status:', response.status);
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const result = await response.json();
    console.log('Translation result:', result);
    
    if (result.success && result.data && result.data.translations) {
      console.log('Translations received:', result.data.translations.length);
      return result.data.translations;
    } else if (result.error) {
      console.error('Translation API error:', result.error);
      throw new Error(result.error);
    } else {
      console.error('Invalid translation response structure:', result);
      throw new Error('    .');
    }
  } catch (error) {
    console.error('Translation error:', error);
    return texts.map(() => '');
  }
}

// Helper functions
function getColumnLetter(columnIndex) {
  let columnLetter = '';
  let tempIndex = columnIndex;
  
  while (tempIndex >= 0) {
    columnLetter = String.fromCharCode((tempIndex % 26) + 65) + columnLetter;
    tempIndex = Math.floor(tempIndex / 26) - 1;
  }
  
  return columnLetter;
}

function formatNumber(value) {
  if (typeof value === 'number') {
    if (Number.isInteger(value)) {
      return value.toLocaleString('ko-KR');
    } else {
      return value.toLocaleString('ko-KR', { minimumFractionDigits: 0, maximumFractionDigits: 2 });
    }
  }
  return value;
}

function showStatus(message, type) {
  const status = document.getElementById('status');
  status.textContent = message;
  status.className = 'status-message ' + type;
  status.style.display = 'block';
  
  if (window.statusTimeout) {
    clearTimeout(window.statusTimeout);
  }
  
  if (type === 'success') {
    window.statusTimeout = setTimeout(() => {
      status.style.display = 'none';
    }, 5000);
  }
}

function clearInput() {
  document.getElementById('commandInput').value = '';
  document.getElementById('status').style.display = 'none';
}

function setCommand(command) {
  document.getElementById('commandInput').value = command;
  document.getElementById('commandInput').focus();
}

function showSettings() {
  showStatus('     AI  .  API    .', 'info');
}

